export default {
  data: function data() {
    return {
      pageLoading: true,
      loading: false,
      shopUrl: "",
      customerName: "",
      words: "",
      vendorName: ""
    };
  },
  created: function created() {
    $("body").addClass("primary-bg");
    $("#viewport").attr("content", "width=device-width,initial-scale=1.0");
  },
  beforeDestroy: function beforeDestroy() {
    $("body").removeClass("primary-bg");
    $("#viewport").attr("content", "width=device-width, user-scalable=yes, initial-scale=0.3, maximum-scale=1.0, minimum-scale=0.3");
  },
  mounted: function mounted() {
    this.getWords();
  },
  methods: {
    getWords: function getWords() {
      var _this = this;

      this.pageLoading = true;
      this.$apiCall("api.Relationship.getRelationshipByRid", {
        rid: decodeURIComponent(this.$route.query.rid)
      }, function (r) {
        _this.pageLoading = false;

        if (r.ErrorCode == "9999") {
          _this.customerName = r.Data.Results.customerName;
          _this.words = r.Data.Results.words;
          _this.vendorName = r.Data.Results.vendorName;
        } else {
          _this.$elementMessage(r.Data.Results.errorMessage, "error");
        }
      });
    },
    getLoginStatus: function getLoginStatus() {
      var _this2 = this;

      this.$apiCall("api.ShopifyAccount.getShopAuthStatus", {
        shopUrl: this.shopUrl
      }, function (r) {
        if (r.ErrorCode == "9999") {
          localStorage.setItem('c_inviteShopUrl', _this2.shopUrl);

          if (r.Data.Results.status) {
            clearInterval(_this2.time);

            if (r.Data.Results.status == 1) {
              localStorage.setItem("c_apiUserId", r.Data.Results.userInfo.id);
              localStorage.setItem("c_apiUserToken", r.Data.Results.userInfo.apiUserToken);
              localStorage.setItem("c_ourMallUserInfo", JSON.stringify(r.Data.Results.userInfo));

              _this2.$store.commit("setUserInfo", r.Data.Results.userInfo);

              _this2.$router.push({
                name: "dashboard"
              });

              _this2.win.close();
            } else {
              _this2.loading = false;

              _this2.$elementMessage(r.Data.Results.errorMessage, "error");

              _this2.win.close();

              setTimeout(function () {
                window.location.reload();
              }, 1000);
            }
          }
        } else {
          clearInterval(_this2.time);

          _this2.$elementMessage(r.Message, "error");
        }
      });
    },
    login: function login() {
      var _this3 = this;

      if (!this.shopUrl) {
        this.$elementMessage("Please enter Shopify store name", "error");
        return false;
      }

      this.win = window.open((process.env.VUE_APP_BASEURL == '/' ? '' : process.env.VUE_APP_BASEURL) + "/blank.html", "Shopify");
      this.loading = true;
      this.$apiCall("api.ShopifyAccount.addShopifyAccount", {
        shopUrl: this.shopUrl,
        getAuth: 1,
        rid: this.$route.query.rid,
        ivc: localStorage.getItem('c_ivc') ? localStorage.getItem('c_ivc') : '',
        utmSource: localStorage.getItem('c_utm')
      }, function (r) {
        if (r.ErrorCode == "9999") {
          _this3.win.location = r.Data.Results;
          _this3.time = setInterval(function () {
            _this3.getLoginStatus();
          }, 2000);
        } else {
          _this3.$elementMessage(r.Message, "error");
        }
      });
    },
    storeInput: function storeInput(val) {
      var codeReg = new RegExp("[A-Za-z0-9-_]+"),
          //正则 英文+数字；
      len = val.length,
          str = "";

      for (var i = 0; i < len; i++) {
        if (codeReg.test(val[i])) {
          str += val[i];
        }
      }

      this.shopUrl = str;
    }
  }
};